  background-size: 224px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 220px;

  position: relative;
  top: 38px;
}

/* second section */

.secSection {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  padding: 0 23%;
  margin-top: 2.3rem;
}

.secSection ul li {
  list-style: none;
  margin-top: 10px;
}

.secSection li a {
  color: #fff;
  text-decoration: none;
  font-size: 12px;
  font-family: "Montserrat", sans-serif;
}

.secSection li:hover,
.secSection li a:hover {
  color: #ff6365;
  transition: all 0.1s ease-in;
}

.third {
  display: flex;
  flex-direction: column;
}

.third h2 {
  font-size: 32px;
  font-family: "Playfair Display", sans-serif;
  color: #fff;
  font-weight: normal;
  margin-bottom: 23px;
}

.third input {
  padding: 13px 54px;
  border: none;
  border-radius: 7px;
  margin-bottom: 23px;
  outline: none;
}

.third span {
  background-color: #45152a;
  padding: 13px 29px;
  color: #fff;

  font-size: 13px;
  display: flex;
  justify-content: center;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: 0.1s ease-in;
  width: 190px;

  /* box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5); */
}

.first li .active {
  color: #ff6365;
}

